import React, { FC, useEffect, useState } from "react";
import { Global } from "@emotion/react";
import { styled } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import { grey, pink } from "@mui/material/colors";
import LocalMoviesOutlinedIcon from "@mui/icons-material/LocalMoviesOutlined";
import Skeleton from "@mui/material/Skeleton";
import Typography from "@mui/material/Typography";
import SwipeableDrawer from "@mui/material/SwipeableDrawer";
import CloseIcon from "@mui/icons-material/Close";
import axios from "axios";

const drawerBleeding = 56;
interface CategoryMenuProps {
  window?: () => Window;
  show: boolean;
  data: any;
  closePage3: () => void;
  title: string;
}

const StyledBox = styled("div")(({ theme }) => ({
  backgroundColor: theme.palette.mode === "light" ? "#fff" : grey[800]
}));

const Puller = styled("div")(({ theme }) => ({
  width: 30,
  height: 6,
  backgroundColor: theme.palette.mode === "light" ? grey[300] : grey[900],
  borderRadius: 3,
  position: "absolute",
  top: 8,
  left: "calc(50% - 15px)"
}));

export const CategoryMenuComponent: FC<CategoryMenuProps> = ({
  window,
  show,
  closePage3,
  title
}) => {
  const [open, setOpen] = React.useState(show);

  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const toggleDrawer = (newOpen: boolean) => () => {
    setOpen(newOpen);
  };

  const handleClose = () => {
    closePage3();
  };

  // if (loading) return <p>Loading...</p>;
  // if (error) return <p>Error: {error}</p>;

  // This is used only for the example
  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <>
      {open && (
        <>
          <CssBaseline />
          <Global
            styles={{
              ".MuiDrawer-root > .MuiPaper-root": {
                height: `calc(85% - ${drawerBleeding}px)`,
                overflow: "visible"
              }
            }}
          />
          <SwipeableDrawer
            container={container}
            anchor="bottom"
            open={open}
            onClose={toggleDrawer(false)}
            onOpen={toggleDrawer(true)}
            swipeAreaWidth={drawerBleeding}
            disableSwipeToOpen={false}
            ModalProps={{
              keepMounted: true
            }}
          >
            <StyledBox
              sx={{
                position: "absolute",
                top: -drawerBleeding,
                borderTopLeftRadius: 8,
                borderTopRightRadius: 8,
                visibility: "visible",
                right: 0,
                left: 0
              }}
            >
              <Puller />
              <Typography sx={{ p: 2, color: pink[500] }}>
                <LocalMoviesOutlinedIcon />{" "}
                <span style={{ fontSize: "20px" }}>{title}</span>
                <CloseIcon
                  style={{ marginLeft: "64%" }}
                  onClick={handleClose}
                ></CloseIcon>
              </Typography>
            </StyledBox>
            <StyledBox
              sx={{
                px: 2,
                pb: 2,
                height: "100%",
                overflow: "auto",
                backgroundColor: "black"
              }}
            >
              {/* <Skeleton variant="rectangular" height="100%" /> */}
              <img
                src="coming_soon.jpeg"
                id="coming_soon"
                style={{ width: "100%", marginTop: "50%" }}
              ></img>
              {/* <div className="instagram-gallery">
                {data &&
                  data["status"] &&
                  data["status"] === "ok" &&
                  data["hashtag"]["edge_hashtag_to_media"]?.map(
                    (edges, index) => (
                      <img
                        key={index}
                        src={edges.node.thumbnail_src}
                        alt={
                          edges.node.edge_media_to_caption.edges[0]?.node.text
                        }
                      />
                    )
                  )}
              </div> */}
            </StyledBox>
          </SwipeableDrawer>
        </>
      )}
    </>
  );
};
